//Style mobile

// Header
.logo {
    border-radius: 2px;
    background-color: #ed140d;
    width: 40px;
    height: 40px;
    border-color: transparent;
    text-indent: -100%;
    overflow: hidden;
}

.search {
    border-radius: 2px;
    background-color: #f7f8fa;
    width: 40px;
    height: 40px;
    border: 0;
    line-height: 40px;
}

.cart {
    color: #6f7274;
    float:right;
    font-size:14px;
    line-height:40px;
    @at-root .dotted {
      margin:0 22px 0 14px;
    }
    @at-root .icon-cart{
      padding-right:14px;
      display:inline-block;
      vertical-align:sub;
    }
}

// Category
nav {
    overflow-x:auto;
    overflow-y:hidden;
    -webkit-overflow-scrolling: touch;
}

.category {
    margin: 0;
    padding: 0;
    background: #ebebeb;
    display:table;
    padding: 0 12px;
    li {
        padding-right: 20px;
        line-height: 61px;
        font-size: 15px;
        display: table-cell;
        white-space: pre;
        &.active {
            border-bottom: 3px solid #ed140d;
        }
    }
}

.header-nav {
    background: $bg-color-1;
    padding:12px 15px;
    margin:0;
    ul {
        margin: 0;
        padding: 0;
    }
    li {
        &:not(.xs-hidden) {
            display: inline-block;
            vertical-align: top;
        }
    }
}

// Description
.description {
  position:relative;
  color:$color-1;
  padding:37px 30px;
  &:after{
      position: absolute;
      content:'';
      top:0;
      bottom:0;
      left:0;
      right:0;
      background:linear-gradient(rgba(0, 0, 0, .6),rgba(0, 0, 0, .6)), url('category.png') center no-repeat;
      background-size:cover;
      z-index:-1;
  }
  h4 {
    margin:0;
    font-size:17px;
    font-weight:normal;
    color: #ffffff;
    line-height: 1.176;
    text-align: left;
  }
  p {
    margin:5px 0 0;
    font-size: 12px;
    color: rgb(255, 255, 255);
    line-height:1.9;
  }
}

// Sort

.sort {
    padding:13px 0;
}

.sort-bottom{
  padding:0 0 13px;
}

// Product list
.items {
    margin: 0;
    padding: 0;
    li{
      padding:0px 0 30px;
      &:last-child{
        padding:0 0 20px;
      }
    }
}



.name{
  padding:10px 0 0;
  margin:0;
  @at-root .item-link {
    font-size: 14px;
    color: #292929;
    font-weight: bold;
    line-height: 2.143;
    text-decoration:none;
  }
  @at-root .price {
    font-size: 14px;
    color: #ed140d;
    line-height: 2.143;
    //text-align: right;
    float:right;
  } 
}

// Img & media
.thumbnail {
    background: #fafafa;
    max-width: 100%;
    //text-indent: 100%;
    border-radius: 2px;
    border:15px solid #ffffff;
    overflow: hidden;
    img[src='#']{
      padding: 50%;
    }
    .name{
      padding-top:15px;
    }
}

.avatar {
    border-radius: 50%;
    border-radius: 50%;
    background-color: #fe4c4c;
    width: 40px;
    height: 40px;
    vertical-align:top;
    display: inline-block;
    overflow: hidden;
    margin-left:13px;
    text-indent: 40px;
}

// Input group
input {
    border-width: 0.5px;
    border-color: #d7d7d7;
    border-style: solid;
    border-radius: 2px;
    background-color: #fafafa;
    width:100%;
    height: 46px;
    &[type="checkbox"],
    &[type="radio"] {
        width: auto;
        outline: none;
        height: auto;
        margin-bottom: 17px;
        &:focus {}
    }
    &[type="text"] {
      text-indent: 14px;
    }
}

.btn-all {
    border-radius: 2px;
    background-color: #ee0d29;
    box-shadow: 0px 3px 8px 0px rgba(238, 13, 41, 0.4);
    border: 0;
    color:$color-1;
    width:48%;
    padding:0;
    height:40px;
    font-size:13px;
    text-transform:uppercase;
    display:inline-block;
    vertical-align:top;
    margin-left:1%;
}

select {
    border-radius: 2px;
    background-color: #e4e8e9;
    border: none;
    width:49%;
    height:40px;
    -webkit-appearance: none;
}

// Newsletter
.newsletter {
    font-size: 13px;
    background-color:#e9eded;
    font-family: "Avenir LT Std";
    color: #292929;
    line-height: 0.833;
    text-align: center;
    padding-bottom:31px;
    h4{
      padding:22px 0;
      margin:0;
      font-size:15px;
      font-family: "Avenir LT Std";
      color: #292929;
    }
    .news-block{
      position:relative;
      margin:0 15px;
      @at-root .send {
        border-radius: 2px;
        background-color: rgb(238, 13, 41);
        box-shadow: 0px 3px 8px 0px rgba(238, 13, 41, 0.4);
        width: 115px;
        height: 30px;
        border:0;
        color:#fff;
        font-size:11px;
        line-height:2.3;
        padding:0;
        text-transform:uppercase;
        position:absolute;
        right:8px;
        top:8px;
      } 
    }
}

// Footer
.footer-link {
    padding: 0;
    text-align: center;
    border-bottom:2px solid #e8ebec;
    line-height:44px;
    margin:0;
    li {
        display: inline-block;
        padding: 0 3px;
        margin: 0;
    }
    a {
        text-decoration: none;
        font-size: 10px;
    }
}

.copyright {
    font-size:10px;
    font-family: "Myriad Pro";
    color: #959090;
    line-height: 2;
    text-align: center;
    margin:0;
    line-height:44px;
}

// Grid
@media (max-width:768px) {
    .xs-hidden {
        display: none;
    }
}


