    font-size:100px;
    font-family:Open Sans;
    font-weight:300;
    margin:87px 0 105px;
  }
  .container{
    background:$bg-color-1;
    padding:78px 415px 100px;
    max-width:1285px;
    margin:auto;
    width:100%;
  }
}

