.bold{
  font-weight:600;
}

// Text

blockquote,
q{
  // font:22px/1.59 $font;
}

p{
  margin:0 0 27px;
  line-height:1.72;
}

blockquote{
  margin:0 0 58px;
  position:relative;
  padding-left:57px;
  p{
    margin:0 0 19px;
    font:inherit;
  }
  &:before{
    content:'';
    border-left:3px solid #0845d1;
    height:100%;
    max-height:122px;
    left:0;
    margin-top:8px;
    position:absolute;
  }
}

.reverse{
  text-align:right;
  padding-right:57px;
  &:before{
    content:none;
  }
  &:after{
    content:'';
    border-right:3px solid #0845d1;
    height:100%;
    right:0;
    bottom:0;
    position:absolute;
  }
}

cite{
  font-style:normal;
  color:$color;
  text-align:right;
  display:block;
  font-size:18px;
}

abbr{
  border-bottom:2px dotted $border-1;
}

// List
ul{
  margin:0 0 18px;
  list-style-type: circle;
}

li{
  // font:18px/1.72 $font;
  list-style:none;
}

ul,
ol{
  ul,
  ol{
    margin:15px 0 18px;
    ul,
    ol{
      margin:8px 0 9px;
    }
  }
}

.unstyled {
    list-style: none;
}

dl{
  line-height:2.67;
}

dt{
  width:220px;
  float:left;
  font-weight:600; 
}

dd{
  margin-left:220px;
}

.left{
  text-align:left;
}

.right{
  text-align:right;
}

.center{
  text-align:center;
}

table{
  margin-bottom:30px;
}

textarea{
  border: 2px solid;
  border-radius: 3px;
  padding-top:40px;
  padding-left:47px;
}

th,td{
  text-align:left;
}



