//New var
$chartColors: (
			   'red-0':(
				  'red-1':$theme-color-1,
			   ),
			   'blue-0':(
				  'blue-1':$theme-color-2,
				  'blue-2':$theme-color-2-1
				),
			   'yellow-0':(
				  'yellow':$theme-color-3,
			   ),
			   'green-0':(
				  'green':$theme-color-4,
			   ),
			   'black-0':(
				  'black-1':$theme-color-5,
				  'black-2':$theme-color-5-1,
				  'black-3':$theme-color-5-2,
				  'black-4':$theme-color-5-3,
				  'black-5':$theme-color-5-4,
				  'black-6':$theme-color-5-5,
				  'black-7':$theme-color-5-6,
				  'black-8':$theme-color-5-7,
				  'black-9':$theme-color-5-8
				)
			  );

@mixin color {
	$index:0;
	@each $color-key, $color-variants in $chartColors {
	  $sub-color: map-values($color-variants);
	  $sub: map-keys($color-variants);
	  $color: map-value($color-key);
	  $index: $index + 1;
	  @for $i from 1 through length($sub-color) {
			&:nth-of-type(#{$index}){
				&:before{
					 background:(nth($sub-color,$i));
				}
				.color-name{
							 &:before{
								 content:"#{nth($sub-color,$i)}";
								 background:transparent!important;
							 }
				}
				:nth-of-type(#{$i}){
					&:before{
						 background:(nth($sub-color,$i));
					}
					.color-name{
							 &:before{
								 content:"#{nth($sub-color,$i)}";
								 background:transparent;
							 }
					}
				}
			}
	  }
	}
}

// Get function this http://codepen.io/jakealbaugh/pen/zGGXwj
@function color($color-key, $color-variants:null) {
  // color variant is optional
  @if ($color-variants != null) {
	// map inception
	@return map-get(map-get($chartColors, $color-key), $color-variants);
  } @else {
	@return map-get($chartColors, $color-key);
  }
}

.color{
	@include color();
}

