input,textarea {
	color: #dadada;
	&:focus {
		color: $theme-color-2;
		box-shadow: 0px 0px 5px 0px rgba(37, 90, 210, 0.65);
	}
	&:hover {
		//color:$theme-color-2-1;
	}
	&.active {
		color: $theme-color-2-1;
	}
	&:disabled {
		background: #f7f7f7;
		color: #d5d5d5;
	}
	&:invalid {
		color: $theme-color-1;
		outline: none;
		&:focus {
			box-shadow: 0px 0px 5px 0px rgba(232, 65, 53, 0.65);
		}
	}
	&.ok {
		&:valid {
			color: #26a65b;
			outline: none;
			&:focus {
				box-shadow: 0px 0px 5px 0px rgba(38, 166, 91, 0.65);
			}
		}
	}
}
/* Checkbox default */
.checkbox{
	position: absolute;
	opacity: 0;
	+ label {
		display: inline-block;
		border: 2px solid $theme-color-2-1;
		color: $color-1;
		width: 18px;
		height: 18px;
		position: relative;
		transition: background .2s;
		cursor: pointer;
		box-sizing: border-box;
		margin-right: 10px;
		vertical-align: middle;
		&:before{
			content: '';
			border-style: solid;
			border-width: 0 0 2px 2px;
			bottom: 4px;
			height: 4px;
			left: 1px;
			margin: auto;
			position: absolute;
			right: 0;
			top: 0;
			width: 8px;
			transform: rotate(-45deg);
			opacity: 0;
			transition: opacity .2s;
		}
	}
	&:checked{
		+ label{
			background: $theme-color-2-1;
			&:before{
				opacity: 1;
			}
		}
	}
}
/* Checkbox slide */
.checkbox-slide:not(checked) {
  position: absolute;
  opacity: 0;
	+ label {
		display: inline-block;
		position: relative; 
		width: 34px;
		height: 18px;
		border-radius: 2px;
		background: $theme-color-5-4;
		box-sizing: border-box;
		cursor: pointer;
		margin-right: 10px;
		vertical-align: middle;
		&:after {
			content: '';
			position: absolute;
			top: 1px;
			left: 1px;
			bottom: 1px;
			width: 16px;
			border-radius: 2px;
			background: $color-1;
			transition: all .2s;
		}
	}
	&:checked{
		+ label {
			background: $theme-color-2-1;
			&:after{
				left: 17px;
			}
		}
	}
	&[disabled]{
		+ label {
			background: $theme-color-5-9;
			&:after{
				background: $theme-color-5-10;
			}
		}
		&[checked]{
			+ label {
				background: $theme-color-5-11;
				&:after{
					background: $theme-color-5-12;
				}
			}
		}
	}
}

/* radio*/
.radio{
	position: absolute;
	opacity: 0;
	+ label {
		box-sizing: border-box;
		width: 16px;
		height: 16px;
		border-radius: 50%;
		border: 1px solid $theme-color-5-13;
		position: relative;
		display: inline-block;
		margin-right: 10px;
		vertical-align: middle;
		&:before{
			content: '';
			background: $theme-color-5-2;
			position: absolute;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			opacity: 0;
		}
	}
	&:checked {
		+ label {
			&:before{
				opacity: 1;
			}
		}
	}
}

