.comment {
	padding: 20px 24px 0;
	border: 1px solid $theme-color-5-4;
	border-radius: 2px;
	margin-bottom: 44px;
	text-align: inherit;
	margin-left: auto;
	margin-right: 0;
	float: none;
	display: inline-block;
	margin-left: 100px;
	vertical-align: top;
	position: relative;
	h5 {
		font-size: 18px;
		font-weight: 400;
		line-height: 1.6;
		color: $theme-color-2;
		margin-bottom: 14px;
	}
	p {
		font-size: 15px;
		margin-bottom: 17px;
	}
}

.comment:after,
.comment:before {
	position: absolute;
	top: 27px;
	left: -16px;
	right: 100%;
	width: 0;
	height: 0;
	display: block;
	content: " ";
	border-color: transparent;
	border-style: solid solid outset;
	pointer-events: none;
}

.comment:before {
	border-right-color: $theme-color-5-4;
	border-width: 8px;
}

.comment:after {
	border-width: 7px;
	border-right-color: $color-1;
	margin-top: 1px;
	margin-left: 2px;
}

